.swal2-popup {
    &.swal2-toast {
        box-sizing: border-box;
        grid-column: 1/4 !important;
        grid-row: 1/4 !important;
        grid-template-columns: min-content auto min-content;
        padding: $swal2-toast-padding;
        overflow-y: hidden;
        background: $swal2-toast-background;
        box-shadow: $swal2-toast-box-shadow;
        pointer-events: all;

        > * {
            grid-column: 2;
        }

        .swal2-title {
            margin: $swal2-toast-title-margin;
            padding: $swal2-toast-title-padding;
            font-size: $swal2-toast-title-font-size;
            text-align: initial;
        }

        .swal2-loading {
            justify-content: center;
        }

        .swal2-input {
            height: $swal2-toast-input-height;
            margin: $swal2-toast-input-margin;
            font-size: $swal2-toast-input-font-size;
        }

        .swal2-validation-message {
            font-size: $swal2-toast-validation-font-size;
        }

        .swal2-footer {
            margin: $swal2-toast-footer-margin;
            padding: $swal2-toast-footer-margin;
            font-size: $swal2-toast-footer-font-size;
        }

        .swal2-close {
            grid-column: 3/3;
            grid-row: 1/99;
            align-self: center;
            width: $swal2-toast-close-button-width;
            height: $swal2-toast-close-button-height;
            margin: $swal2-toast-close-button-margin;
            font-size: $swal2-toast-close-button-font-size;
        }

        .swal2-html-container {
            margin: $swal2-toast-html-container-margin;
            padding: $swal2-toast-html-container-padding;
            overflow: initial;
            font-size: $swal2-toast-html-container-font-size;
            text-align: initial;

            &:empty {
                padding: 0;
            }
        }

        .swal2-loader {
            grid-column: 1;
            grid-row: 1/99;
            align-self: center;
            width: 2em;
            height: 2em;
            margin: 0.25em;
        }

        .swal2-icon {
            grid-column: 1;
            grid-row: 1/99;
            align-self: center;
            width: 2em;
            min-width: 2em;
            height: 2em;
            margin: 0 0.5em 0 0;

            .swal2-icon-content {
                display: flex;
                align-items: center;
                font-size: $swal2-toast-icon-font-size;
                font-weight: bold;
            }

            &.swal2-success {
                .swal2-success-ring {
                    width: 2em;
                    height: 2em;
                }
            }

            &.swal2-error {
                [class^='swal2-x-mark-line'] {
                    top: 0.875em;
                    width: 1.375em;

                    &[class$='left'] {
                        left: 0.3125em;
                    }

                    &[class$='right'] {
                        right: 0.3125em;
                    }
                }
            }
        }

        .swal2-actions {
            justify-content: flex-start;
            height: auto;
            margin: 0;
            margin-top: 0.5em;
            padding: 0 0.5em;
        }

        .swal2-styled {
            margin: 0.25em 0.5em;
            padding: 0.4em 0.6em;
            font-size: $swal2-toast-buttons-font-size;
        }

        .swal2-success {
            border-color: $swal2-success;

            [class^='swal2-success-circular-line'] {
                // Emulate moving circular line
                position: absolute;
                width: 1.6em;
                height: 3em;
                transform: rotate(45deg);
                border-radius: 50%;

                &[class$='left'] {
                    top: -0.8em;
                    left: -0.5em;
                    transform: rotate(-45deg);
                    transform-origin: 2em 2em;
                    border-radius: 4em 0 0 4em;
                }

                &[class$='right'] {
                    top: -0.25em;
                    left: 0.9375em;
                    transform-origin: 0 1.5em;
                    border-radius: 0 4em 4em 0;
                }
            }

            .swal2-success-ring {
                width: 2em;
                height: 2em;
            }

            .swal2-success-fix {
                top: 0;
                left: 0.4375em;
                width: 0.4375em;
                height: 2.6875em;
            }

            [class^='swal2-success-line'] {
                height: 0.3125em;

                &[class$='tip'] {
                    top: 1.125em;
                    left: 0.1875em;
                    width: 0.75em;
                }

                &[class$='long'] {
                    top: 0.9375em;
                    right: 0.1875em;
                    width: 1.375em;
                }
            }

            &.swal2-icon-show {
                @if $swal2-icon-animations {
                    .swal2-success-line-tip {
                        animation: swal2-toast-animate-success-line-tip 0.75s;
                    }

                    .swal2-success-line-long {
                        animation: swal2-toast-animate-success-line-long 0.75s;
                    }
                }
            }
        }

        &.swal2-show {
            animation: $swal2-toast-show-animation;
        }

        &.swal2-hide {
            animation: $swal2-toast-hide-animation;
        }
    }
}
